<template>
  <div class="space" ref="space" :style="{background: bgImgUrl}">
    <img src="../assets/return.png" class="return" @click="returnHome">
    <img :src="titleUrl" class="spaceTitle">
    <!-- <img src="../assets/spaceBg.jpg" class="spaceBg"> -->
    <!-- <div class="touchDiv"> -->
      <v-touch v-on:swipeleft="swiperleft" v-on:swiperight="swiperright" class="touchDiv">
        <img src="../assets/left.png" class="wrapper touchLeft">
        <img src="../assets/right.png" class="wrapper touchRight">
      </v-touch>
      <!-- <v-touch  v-on:swiperight="swiperright">
        <img src="../assets/right.png" class="wrapper touchRight">
      </v-touch> -->
    <!-- </div> -->
    <div class="menu-container" ref="menuContainer">
      <!--  -->
      <!-- <img src="../assets/space4.jpg" class="showImg"> -->
      <img :src="explainObj.picture" class="showImg">
      <div class="musicPlayDiv">
        <img v-if="!isplay" src="../assets/play.png" class="playIcon" @click="playAudio">
        <img v-else src="../assets/pause.png" class="playIcon" @click="pauseAudio">
        <div class="playProsess">
          <div class="playFill" :style="{width: playWidth}"></div>
        </div>
      </div>
      <!-- <p class="explainTitle"></p>
      <p class="explainText">360
        360
        360
      </p> -->
      <p class="explainTitle">{{explainObj.name}}</p>
      <p class="explainText">{{explainObj.content}}</p>
      <button class="detailBtn" @click="openDetail"></button>
    </div>
    <div class="mask" v-if="modalFlag" @click="closeDetail"></div>
    <div class="modal" v-if="modalFlag">
      <p class="explainTitle">{{explainObj.name}}</p>
      <p class="modalText">{{explainObj.content}}</p>
      <!-- <p class="explainTitle"></p>
      <p class="modalText">360
        360
        360
      </p> -->
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      clientHeight: '',
      musicPlay: {},
      audioTime: 0,
      audioInterval: '',
      isplay: false,
      playWidth: 0,
      count: 0,
      id: '',
      modalFlag: false,
      bgImgUrl: 'url(./static/image/spaceBg.jpg)',
      titleUrl: './static/image/spaceTitle.png',
      explainObj: {},
      artInfoList: []
      // artInfoList: [
      //   {
      //     name: '',
      //     content: '',
      //     picture: '../assets/space1.png'
      //   },
      //   {
      //     name: '',
      //     content: '',
      //     picture: '../assets/space2.jpg'
      //   },
      //   {
      //     name: '',
      //     content: '',
      //     picture: '../assets/space3.jpg'
      //   },
      //   {
      //     name: '4D',
      //     content: '4D4D4D',
      //     picture: '../assets/space4.jpg'
      //   }
      // ]
    }
  },
  methods: {
    swiperleft: function () {
      this.count = this.count + 1
      if (this.count >= this.artInfoList.length) {
        return false
      } else {
        this.$router.push({
          path: 'space',
          query: { count: this.count }
        })
        this.explainObj = this.artInfoList[this.count]
      }
    },
    swiperright: function () {
      if (this.count === 0) {
        return false
      } else {
        this.count = this.count - 1
        this.$router.push({
          path: 'space',
          query: { count: this.count }
        })
        this.explainObj = this.artInfoList[this.count]
      }
    },
    setAudioInterval () {
      this.audioInterval = setInterval(() => {
        this.getAudioTime()
        if (this.musicPlay.ended) {
          clearInterval(this.audioInterval)
          this.audioTime = 0
          this.isPlay = false
        }
        this.musicPlay.paused ? this.isPlay = false : this.isPlay = true
      }, 500)
    },
    playAudio () {
      clearInterval(this.audioInterval)
      this.getAudioTime()
      this.setAudioInterval()
      this.musicPlay.play()
      this.isplay = true
    },
    pauseAudio () {
      this.musicPlay.pause()
      this.isplay = false
    },
    getAudioTime () {
      var audioPlayer = this.musicPlay
      this.audioTime = (audioPlayer.currentTime * 100 / audioPlayer.duration)
      this.playWidth = (this.audioTime) + '%'
    },
    returnHome () {
      this.$router.push({
        path: '/'
      })
    },
    openDetail () {
      this.modalFlag = true
    },
    closeDetail () {
      this.modalFlag = false
    }
  },
  mounted () {
    var that = this
    that.clientHeight = document.documentElement.clientHeight
    that.$refs.space.style.height = that.clientHeight + 'px'
    that.musicPlay = new Audio('./static/audio/music.m4a')
    that.setAudioInterval()
    that.count = that.$route.query.count
    that.id = that.$route.query.id
    if (that.id === 1) {
      that.bgImgUrl = 'url(./static/image/spaceBg.jpg)'
      that.titleUrl = './static/image/spaceTitle.png'
    } else if (that.id === 2) {
      that.bgImgUrl = 'url(./static/image/robotBg.png)'
      that.titleUrl = './static/image/robotTitle.png'
    } else if (that.id === 3) {
      that.bgImgUrl = 'url(./static/image/childBg.jpg)'
      that.titleUrl = './static/image/childTitle.png'
    } else if (that.id === 4) {
      that.bgImgUrl = 'url(./static/image/informationBg.jpg)'
      that.titleUrl = './static/image/informationTitle.png'
    } else if (that.id === 5) {
      that.bgImgUrl = 'url(./static/image/basicBg.jpg)'
      that.titleUrl = './static/image/basicTitle.png'
    } else if (that.id === 6) {
      that.bgImgUrl = 'url(./static/image/lifeBg.jpg)'
      that.titleUrl = './static/image/lifeTitle.png'
    } else if (that.id === 7) {
      that.bgImgUrl = 'url(./static/image/safetyBg.jpg)'
      that.titleUrl = './static/image/safetyTitle.png'
    } else if (that.id === 8) {
      that.bgImgUrl = 'url(./static/image/cinemaBg.jpg)'
      that.titleUrl = './static/image/cinemaTitle.png'
    }
    that.httpPost({
      url: '/api/query_all_second',
      data: {
        id: that.id
      },
      successBack: function (data) {
        // alert(data);
        if (data.retCode === 1) {
          // alert(data)
          that.artInfoList = data.list
          that.explainObj = that.artInfoList[that.count]
        } else {
          that.tips = '' + data.msg
        }
      }
    })
    // document.addEventListener('click', function (e) {
    //   alert(2222)
    //   if (!!that.$refs.modal.contains(e.target)) {
    //     alert(33333)
    //     return
    //   } else {
    //     alert(1111)
    //     that.modalFlag = false
    //   }
    // })
  }
}
</script>

<style>
.space{
  /* background: url('../assets/spaceBg.jpg'); */
  /* background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed; */
  background-size: 100% 100%;
}
.spaceTitle{
  margin-top: 8%;
  width: 210px;
  height: 57px;
}
.wrapper{
  width: 50px;
  height: 55px;
}
.touchDiv{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
}
.touchLeft{
  position: absolute;
  left: 1%;
  top: 24%;
}
.touchRight{
  position: absolute;
  right: 1%;
  top: 24%;
}
.showImg{
  margin-top: 10%;
  width: 240px;
  height: 154px;
  border: 5px solid #ffffff;
  box-shadow: 0 0 20px #ffffff;
}
.explainTitle{
  font-weight: 500;
  text-shadow: 0 0 10px #ffffff;
  margin-top: 60px;
  font-size: 24px;
  color: #ffffff;
}
.explainText{
  font-size: 13px;
  color: #ffffff;
  margin: 23px 55px;
  line-height: 20px;
  text-align: left;
  max-height: 200px;
  text-indent: 2em;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 10;/**/
  overflow: hidden;
}
.playIcon{
  width: 36px;
  height: 36px;
}
.playProsess{
  margin-bottom: 15px;
  width: 230px;
  height: 8px;
  border-radius: 4px;
  background: -webkit-linear-gradient(top,#B4B4B4,#EFEFEF);
  display: inline-block;
  position: relative;
  box-shadow: 0 0 10px #ffffff;
}
.playFill{
  top: 0;
  position: absolute;
  height: 8px;
  border-radius: 4px;
  left: 0;
  background: -webkit-linear-gradient(top,#65A3BA,#5996D4);
}
.musicPlayDiv{
  top: 39%;
  left: 12%;
  position: absolute;
  z-index: 199;
}
.return{
  height: 50px;
  width: 55px;
  position: absolute;
  left: 1%;
  top: 1%;
  z-index:199;
}
.detailBtn{
  height:25px;
  width: 100px;
  background-color: rgba(255, 255, 255, 0.5);
  color: #ffffff;
  font-size:10px;
  border:none;
  position: fixed;
  bottom: 3%;
  left: 50%;
  margin-left: -50px;
  z-index:199;
}
.mask{
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: #000;
  z-index: 299;
  opacity: 0.5;
}
.modal{
  width: 300px;
  height: 446px;
  position: absolute;
  left: 50%;
  margin-left: -150px;
  top: 50%;
  margin-top: -223px;
  text-align: center;
  z-index: 399;
  background: url('../assets/modalImg.png') no-repeat;
  background-size: 100% 100%;
}
.modalText{
  font-size: 13px;
  color: #ffffff;
  margin: 23px 55px;
  line-height: 20px;
  text-align: left;
  max-height: 260px;
  text-indent: 2em;
  overflow-y: auto;
}
</style>



