<template>
  <div class="home">
    <transition name="fade">
      <swiper id="swiperBox" v-bind:options="swiperOption" ref="mySwiper">
        <!--  -->
        <swiper-slide class="swiper-slide1">
          <div class="page">
            <!-- <div class="cinema" @click="openCinema(8)"> -->
            <div class="cinema" @click="openSpace(8)">
              <img src="../assets/4DCinema.png">
              <p class="areaText">4D</p>
            </div>
            <!-- <div class="lifeScience" @click="openLife(6)"> -->
            <div class="lifeScience" @click="openSpace(6)">
              <img src="../assets/lifeScience.png" class="imgIcon">
              <p class="areaText"></p>
            </div>
            <div class="spaceExplore" @click="openSpace(1)">
              <img src="../assets/spaceExplore.png" class="imgIcon">
              <p class="areaText"></p>
            </div>
            <!-- <div class="robot" @click="openRobot(2)">openSpace -->
            <div class="robot" @click="openSpace(2)">
              <img src="../assets/robot.png" class="imgIcon">
              <p class="areaText"></p>
            </div>
            <!-- <div class="information" @click="openInformation(4)"> -->
            <div class="information" @click="openSpace(4)">
              <img src="../assets/information.png" class="imgIcon">
              <p class="areaText"></p>
            </div>
            <img src="../assets/next.png" class="next">
          </div>
        </swiper-slide>
        <!--  -->
        <swiper-slide class="swiper-slide2">
          <div class="page">
            <!-- <div class="basicScience" @click="openBasic(5)"> -->
            <div class="basicScience" @click="openSpace(5)">
              <img src="../assets/basicScience.png" class="imgIcon">
              <p class="areaText"></p>
            </div>
            <!-- <div class="publicSafety" @click="openSafety(7)"> -->
            <div class="publicSafety" @click="openSpace(7)">
              <img src="../assets/publicSafety.png" class="imgIcon">
              <p class="areaText"></p>
            </div>
            <!-- <div class="childPlace" @click="openChild(3)"> -->
            <div class="childPlace" @click="openSpace(3)">
              <img src="../assets/childPlace.png" class="imgIcon">
              <p class="areaText"></p>
            </div>
            <p class="areaOnlyText imagin"></p>
            <p class="areaOnlyText report"></p>
            <img src="../assets/previous.png" class="next">
          </div>
        </swiper-slide>
      </swiper>
    </transition>
  </div>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  data () {
    return {
      swiperOption: {
        // swiper configs swiperapi
        notNextTick: true, // notNextTicknotNextTicktrueNextTickswiperswiperswipertrue
        direction: 'vertical', // 
        grabCursor: true, // Swiper
        setWrapperSize: true, // Swiperflexbox(display: flex)Wrapperslidesflexbox
        autoHeight: true, // truewrappercontainerslide
        slidesPerView: 1, // sliderslides(carousel)loop 'auto'slidesloop'auto'loopedSlides
        mousewheel: false, // Swiperfalse
        mousewheelControl: false, // 
        height: window.innerHeight, // 
        resistanceRatio: 0, // slide0
        observeParents: true, // observeSwiperSwiperwindow.resizeSwiper
        // debugger
        debugger: true,
        // swiperswiper
        onTransitionStart (swiper) {
          console.log(swiper)
        },
        on: {
          // swiper
          slideChange: () => {
            let swiper = this.$refs.mySwiper.swiper
            console.log(swiper.activeIndex) // 
          }
        }
      }
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    }
  },
  methods: {
    // openCinema (id) {
    //   this.$router.push({
    //     path: 'cinema',
    //     query: { count: 0, id: id}
    //   })
    // },
    // openLife (id) {
    //   this.$router.push({
    //     path: 'life',
    //     query: { count: 0, id: id}
    //   })
    // },
    openSpace (id) {
      this.$router.push({
        path: 'space',
        query: {count: 0, id: id}
      })
    }
    // openRobot (id) {
    //   this.$router.push({
    //     path: 'robot',
    //     query: { count: 0, id: id}
    //   })
    // },
    // openInformation (id) {
    //   this.$router.push({
    //     path: 'information',
    //     query: { count: 0, id: id}
    //   })
    // },
    // openBasic (id) {
    //   this.$router.push({
    //     path: 'basic',
    //     query: { count: 0, id: id}
    //   })
    // },
    // openSafety (id) {
    //   this.$router.push({
    //     path: 'safety',
    //     query: { count: 0, id: id}
    //   })
    // },
    // openChild (id) {
    //   this.$router.push({
    //     path: 'child',
    //     query: { count: 0, id: id}
    //   })
    // },
  },
  mounted () {
    console.log('this is current swiper instance object', this.swiper)
    this.swiper.slideTo(0, 1000, false) // 
    // var that = this
    // that.httpPost({
    //   url: '/api/query_all_one',
    //   successBack: function (data) {
    //     // alert(data);
    //     if (data.ret === 1) {
    //       alert(data)
    //     } else {
    //       that.tips = '' + data.msg
    //     }
    //   }
    // })
  }
}
</script>

<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}
.swiper-slide1{
  background: url('../assets/firstFloor.jpg');
  /* background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed; */
  background-size: 100% 100%;
}
.swiper-slide2{
  background: url('../assets/secondFloor.jpg');
  background-size: 100% 100%;
}
.areaText{
  font-size: 10px;
  color: #ffffff;
}
.cinema{
  position: absolute;
  left: 19%;
  top: 16%;
}
.cinema img{
  width: 60px;
  height: 60px;
}
.lifeScience{
  position: absolute;
  left: 67%;
  top: 25%;
}
/* .lifeScience img{
  width: 65px;
  height: 57px;
} */
.spaceExplore{
  position: absolute;
  left: 39%;
  top: 40%;
}
/* .spaceExplore img{
  width: 66px;
  height: 60px;
} */
.robot{
  position: absolute;
  left: 66.5%;
  top: 48%;
}
/* .robot img{
  width: 65px;
  height: 57px;
} */
.information{
  position: absolute;
  left: 33%;
  top: 72.5%;
}
/* .information img{
  width: 62px;
  height: 54px;
} */
.basicScience{
  position: absolute;
  left: 65.5%;
  top: 35%;
}
/* .basicScience img{
  width: 59px;
  height: 52px;
} */
.publicSafety{
