}
/* .publicSafety img{
  width: 55px;
  height: 50px;
} */
.childPlace{
  position: absolute;
  left: 14%;
  top: 73%;
}
/* .childPlace img{
  width: 45px;
  height: 40px;
} */
.imgIcon{
  width: 65px;
  height: 57px;
}
.areaOnlyText{
  font-size:10px;
  color: #b5d6fc;
}
.imagin{
  position: absolute;
  left: 57%;
  top: 72%;
}
.report{
  position: absolute;
  left: 71%;
  top: 79%;
}
.next{
  width: 60px;
  height: 50px;
  position: absolute;
  bottom: 20px;
  left: 50%;
  margin-left: -30px;
}
</style>



